<template>
	<div class="vote-page">
		<div class="vote-header">
			<div class="vote-header-back" @click="$router.back()">
				<i class="fa fa-angle-left fa-2x"></i>
				<span>{{$t('pageCommon.back')}}</span>
			</div>
		</div>
		<el-table
			:data="tableData"
			stripe
			class='vote-table'>
			<el-table-column
				prop="num"
				:label="$t('votePage.prop.num')"
				width="104">
			</el-table-column>
			<el-table-column
				prop="title"
				:label="$t('votePage.prop.title')">
			</el-table-column>
			<el-table-column
				prop="time"
				:label="$t('votePage.prop.time')">
			</el-table-column>			
			<el-table-column
				prop="creator"
				:label="$t('votePage.prop.name')">
			</el-table-column>
            <el-table-column
                :label="$t('votePage.prop.Management')"
                width="160">
                <template scope="scope">
					<el-button class='vote-table-check' @click = 'checkRow(scope.$index, scope.row)'>{{$t('votePage.View')}}</el-button>
                </template>
            </el-table-column>
		</el-table>
		<el-pagination
			mini
			layout="prev, pager, next"
			:total="pageNum*10" class='vote-pagination' @current-change="currentPage">
		</el-pagination>
		
	    <transition name="checkVote">
	      	<div class="checkVote-shade" v-if="checkVoteCtrl" @click.stop="checkVoteCtrl=false">
		      	<div class="checkVote-window" @click.stop="checkVoteCtrl=true">
		        	<div class="add-checkVote-top">
		          		<span>{{$t('votePage.checkVote')}}</span>
		          		<i class="fa fa-close fa-2x" @click.stop="checkVoteCtrl=false" role='button'></i>
		        	</div>
		        	<div class="vote-option">
		        		<h4>{{$t('votePage.window.title')}}</h4>
		        		<p class="vote-option-title">{{voteMsg.title}}</p>
		        		<h4>{{$t('votePage.window.content')}}</h4>
  				  		<el-radio-group v-model="radioIndex" v-if="String(voteMsg.type)==='1'">
		   					<el-radio v-for="(item, index) in voteMsg.options" :label="index" :key="item.title" class="vote-el-option">{{item.title}}
		   						<span class="option-vote-result">{{$t('votePage.window.result')}}：{{item.select.length}}</span>
		   					</el-radio>
  						</el-radio-group>			
					  	<el-checkbox-group v-model="checkboxIndex" v-if="String(voteMsg.type)==='2'">
					    	<el-checkbox v-for="(item, index) in voteMsg.options" :label="index" :key="item.title" class="vote-el-option">{{item.title}}
					    		<span class="option-vote-result">{{$t('votePage.window.result')}}：{{item.select.length}}</span>
					    	</el-checkbox>
					  	</el-checkbox-group>
		        	</div>
		      	</div>	      	
	      	</div>
	    </transition>
	</div>
</template>

<script>
	import './vote.scss'
    export default {
        name:'Vote',
		data() {
			return {
				tableData: [],
				radioIndex : '1',
				checkboxIndex : [],
				pageNum : 1,
				checkVoteCtrl : false,
				voteMsg : {},
		        ReqList: {
		          	where: {
		              	offset: 0,
		              	limit: 30,
		              	sortBy: 'createdAt',
		              	order: -1
		            }
		        }				
			}
		},
		methods : {
			dateFilter(date){
				let arr = date.split('.')[0].split('T')
				let arrFirst = arr[0].split('-')
				let resultTime = arrFirst[0] + '-' + arrFirst[1] + '-' + arrFirst[2] + ' ' + arr[1]
				return resultTime;
			},
			currentPage(val){
				this.ReqList.where.offset = (val - 1) * 30
	      		this.$http.GetVoteList(this.ReqList)
	      		.then((data)=>{
	      			let tempTableData = data.data.list
	      			for(let i = 0; i < tempTableData.length; i++){
	      				tempTableData[i]["num"] = (i + 1) + (val - 1) * 30
	      				tempTableData[i]["time"] = this.dateFilter(tempTableData[i].createdAt)
	      			}
					this.tableData = tempTableData
	      			this.pageNum = Math.ceil(data.data.count / 30)
	      		})
	      		.catch(error=>{
	      			console.log(error)
	      		})
			},
			checkRow(index, row){
				this.voteMsg = row
				this.checkVoteCtrl = true
			}
		},
		created(){
      		this.currentPage(1)
		}
    }	
</script>


